import axios from 'axios'
import { USER_DETAILS_RESET, USER_DETAILS_FAIL, USER_DETAILS_REQUEST, USER_DETAILS_SUCCESS, USER_LOGIN_FAIL, USER_LOGIN_LOGOUT, USER_LOGIN_REQUEST, USER_LOGIN_SUCCESS, USER_REGISTER_FAIL, USER_REGISTER_REQUEST, USER_REGISTER_SUCCESS, USER_UPDATE_PROFILE_FAIL, USER_UPDATE_PROFILE_REQUEST, USER_UPDATE_PROFILE_SUCCESS, USER_LIST_REQUEST, USER_LIST_SUCCESS, USER_LIST_FAIL, USER_LIST_RESET, USER_DELETE_REQUEST, USER_DELETE_SUCCESS, USER_DELETE_FAIL, USER_UPDATE_REQUEST, USER_UPDATE_SUCCESS, USER_UPDATE_FAIL } from "../constants/userConstants"
import { ORDER_LIST_MY_RESET, ORDER_LIST_RESET } from '../constants/orderConstants'
import { CART_RESET } from '../constants/cartConstants'

export const login = (email, password) => async (dispatch) => {
        try {
            dispatch({
                type: USER_LOGIN_REQUEST
            })

            const config = {
                headers: {
                    'Content-Type': 'application/json'
                }
            }
            const { data } = await axios.post('/api/users/login', {email, password} ,
            config)

            dispatch({
                type: USER_LOGIN_SUCCESS,
                payload: data
            })

            localStorage.setItem('userInfo', JSON.stringify(data))
        } catch (error) {
            
            dispatch({
                type: USER_LOGIN_FAIL,
                payload: error.response && error.response.data && error.response.data.message 
                ? error.response.data.message
                : error.response && error.response.status === 401
                ? 'Invalid email or password'//user a default message for 401 Unauthorized status
                : error.message,
                
            })
            //console.log(error)
           //console.log(error.response.data.message)
           // console.log(error.response.data.message)
          // console.log(error.response.message)
        }
        
}

export const register = (name, email, password) => async (dispatch) => {
    try {
        dispatch({
            type: USER_REGISTER_REQUEST
        })

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }
        const { data } = await axios.post('/api/users', {name, email, password} ,
        config)

        dispatch({
            type: USER_REGISTER_SUCCESS,
            payload: data,
        })

        //log user in after successful registration
        dispatch({
            type: USER_LOGIN_SUCCESS,
            payload: data,
        })

        localStorage.setItem('userInfo', JSON.stringify(data))
    } catch (error) {
        
        dispatch({
            type: USER_REGISTER_FAIL,
            payload: error.response && error.response.data && error.response.data.message 
            ? error.response.data.message
            : error.response && error.response.status === 401
            ? 'Invalid email or password'//user a default message for 401 Unauthorized status
            : error.message,
            
        })
    }
    
}


export const getUserDetails = (id) => async (dispatch, getState) => {
    try {
        dispatch({
            type: USER_DETAILS_REQUEST,
        })

        const { userLogin: { userInfo } } = getState()

        const config = {
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            },
        }
        const { data } = await axios.get(`/api/users/${id}`, config)

        dispatch({
            type: USER_DETAILS_SUCCESS,
            payload: data,
        })


    } catch (error) {
        
        dispatch({
            type: USER_DETAILS_FAIL,
            payload: error.response && error.response.data && error.response.data.message 
            ? error.response.data.message
            : error.response && error.response.status === 401
            ? 'Invalid email or password'//use a default message for 401 Unauthorized status
            : error.message,
            
        })
    }
    
}

