import { CART_ADD_ITEM, CART_RESET } from "../constants/cartConstants";
import { CART_REMOVE_ITEM } from "../constants/cartConstants";
import { CART_SAVE_SHIPPING_ADDRESS } from "../constants/cartConstants";
import { CART_SAVE_PAYMENT_METHOD } from "../constants/cartConstants";

export const cartReducer = (state = {cartItems: [], shippingAddress: {} }, 
    action
    ) =>{
    switch(action.type){
        case CART_ADD_ITEM:
            const item = action.payload

            const existItem = state.cartItems.find(x => x.product === item.product)
            if(existItem){
                return {
                    ...state,
                    cartItems: state.cartItems.map(x => x.product === existItem.product ? item : x)
                }

            }else{
                return{
                    ...state,
                    cartItems: [...state.cartItems, item]
                }
            }

            case CART_REMOVE_ITEM:
                return {
                    ...state,
                    cartItems: state.cartItems.filter(x => x.product !== action.payload)
                }
            case CART_SAVE_SHIPPING_ADDRESS:
                //console.log("saving shipping address to state", action.payload);
                return {
                    ...state,
                    shippingAddress:  action.payload
                }

            case CART_SAVE_PAYMENT_METHOD:
                //console.log("saving shipping address to state", action.payload);
                return {
                    ...state,
                    paymentMethod:  action.payload
                }

            case CART_RESET:
                return { cartItems: [] }
                
                
        default:
            return state
      
    }
    

}